import React, { useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';

import conditionerIcon from './img/air-conditioner.svg';
import heaterIcon from './img/heater.svg';
import githubIcon from './img/github-logo.svg';
import weatherIcon from './img/weather.svg';
import humidityIcon from './img/humidity.svg';

import Form from 'react-bootstrap/Form';
import Col from 'react-bootstrap/Col';

const minNormal = 18;
const maxNormal = 24;

function App() {
  const [automatic, setAutomatic] = useState(true);
  const [tempOutside, setTempOutside] = useState(60);
  const [tempInside, setTempInside] = useState(65);

  const [conditioner, setConditioner] = useState(false);
  const [heater, setHeater] = useState(true);

  useEffect( () => {
    if (automatic) {
      let outside = tempOutside-40;
      let inside = tempInside-40;
      if (outside > maxNormal && inside > maxNormal) {
        setConditioner(true);
        setHeater(false);
      } else if(outside <= minNormal && inside > maxNormal) {
        setConditioner(false);
        setHeater(false);
      } else if(outside <= maxNormal && outside >= minNormal && inside > maxNormal) {
        setConditioner(true);
        setHeater(false);
      }

      else if (outside > maxNormal && inside <= maxNormal && inside >= minNormal) {
        setConditioner(false);
        setHeater(false);
      } else if(outside <= minNormal && inside <= maxNormal && inside >= minNormal) {
        setConditioner(false);
        setHeater(false);
      } else if(outside <= maxNormal && outside >= minNormal && inside <= maxNormal && inside >= minNormal) {
        setConditioner(false);
        setHeater(false);
      }

      else if (outside > maxNormal && inside < minNormal) {
        setConditioner(false);
        setHeater(false);
      } else if(outside <= minNormal && inside < minNormal) {
        setConditioner(false);
        setHeater(true);
      } else if(outside <= maxNormal && outside >= minNormal && inside < minNormal) {
        setConditioner(false);
        setHeater(false);
      }
    }

  }, [ tempOutside, tempInside, automatic ]);

  return (
    <div className="App">
      <nav className="navbar navbar-dark bg-primary navbar-expand-lg">
        <div className="container">
          <span className="navbar-brand font-weight-bold">Climate control</span>
          <button className="navbar-toggler" type="button"
                  data-toggle="collapse" data-target="#navbarSupportedContent"
                  aria-controls="navbarSupportedContent" aria-expanded="false"
                  aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"/>
          </button>

          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav mr-auto">
            </ul>
            <a className="text-white " href="https://github.com/vozbrann">
              <img style={{paddingBottom: "5px", height: "25px", filter: "invert(1)"}}
                   className="mr-1"
                   src={githubIcon} alt=""/>
                Roman Vozbrannyi
            </a>
          </div>
        </div>
      </nav>

      <div className="container mt-3">
        <div className="row">
          {/*<div className="col-12 alert alert-warning" role="alert">*/}
          {/*  No connection*/}
          {/*</div>*/}
          <div className="col-12 d-flex mb-3 justify-content-end">
            <h3>
              <span className="mr-5">Automatic mode:</span>
              <span className="btn-group btn-group-lg rounded border"
                    role="group" aria-label="First group">
                    <button type="button"
                            className={"btn " + (automatic ? "btn-primary" : "btn-light")}
                            onClick={() => setAutomatic(true)}
                    >On</button>
                    <button type="button"
                            className={"btn " + (automatic ? "btn-light" : "btn-primary")}
                            onClick={() => setAutomatic(false)}
                    >Off</button>
                </span>
            </h3>
          </div>
        </div>
      </div>

      <div className="container pt-3">
        <div className="row">
          <div className="col-12">
            <div className="container">
              <div className="row">
                <div className="col-md-6">
                  <h3>
                    Inside:
                    <img style={{height: "30px", paddingBottom: "5px"}}
                         src={weatherIcon} className="ml-3" alt=""/>{tempInside-40}<sup>o</sup>C
                      <img
                        style={{height: "30px",  paddingBottom: "5px", marginLeft: "15px"}}
                        src={humidityIcon} alt=""/>60%
                  </h3>
                </div>
                <div className="col-md-6 text-md-right">
                  <h3>
                    Outside:
                    <img style={{height: "30px", paddingBottom: "5px"}}
                         src={weatherIcon} className="ml-3" alt=""/>{tempOutside-40}<sup>o</sup>C
                      <img
                        style={{height: "30px",  paddingBottom: "5px", marginLeft: "15px"}}
                        src={humidityIcon} alt=""/>82%
                  </h3>
                </div>
              </div>
            </div>
          </div>
          <div className="col-12 shadow mb-3 p-4">
            <div className="container">
              <div className="row">
                <div
                  className="col-md-9 d-flex flex-column flex-md-row align-items-center">
                  <img style={{height: "70px"}} src={conditionerIcon}
                       alt=""/>
                    <h2 className="ml-md-5">Air conditioner</h2>
                </div>
                <div className="col">
                  <div className="text-md-right text-center text-md-left">
                    <div className="btn-group btn-group-lg rounded border"
                         role="group" aria-label="First group">
                      <button
                        type="button"
                        disabled={automatic}
                        className={"btn " + (conditioner ? "btn-primary" : "btn-light")}
                        onClick={() => setConditioner(true)}
                      >
                        On
                      </button>
                      <button
                        type="button"
                        disabled={automatic}
                        className={"btn " + (!conditioner ? "btn-primary" : "btn-light")}
                        onClick={() => setConditioner(false)}
                      >
                        Off
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="col-12 shadow mb-3 p-4">
            <div className="container">
              <div className="row">
                <div className="col-md-9 d-flex flex-column flex-md-row align-items-center">
                  <img style={{height: "70px"}} src={heaterIcon} alt=""/>
                    <h2 className="ml-md-5">Heater</h2>
                </div>
                <div className="col">
                  <div className="text-md-right text-center">
                    <div className="btn-group btn-group-lg rounded border"
                         role="group" aria-label="First group">
                      <button
                        type="button"
                        disabled={automatic}
                        className={"btn " + (heater ? "btn-primary" : "btn-light")}
                        onClick={() => setHeater(true)}
                      >
                        On
                      </button>
                      <button
                        type="button"
                        disabled={automatic}
                        className={"btn " + (!heater ? "btn-primary" : "btn-light")}
                        onClick={() => setHeater(false)}
                      >
                        Off
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="container shadow p-4">
        <h2>Simulation</h2>

        <Form>
          <Form.Row>
            <Form.Group className="px-5" as={Col} md={6} controlId="formBasicRangeCustom">
              <Form.Label className="h3">
                Inside:
                <img style={{height: "30px", paddingBottom: "5px"}}
                     src={weatherIcon} className="ml-3" alt=""/>{tempInside-40}<sup>o</sup>C
              </Form.Label>
              <Form.Control value={tempInside} onChange={(e)=> {setTempInside(e.target.value)}} type="range" custom />
            </Form.Group>
            <Form.Group className="px-5" as={Col} md={6} controlId="formBasicRangeCustom">
              <Form.Label className="h3">
                Outside:
                <img style={{height: "30px", paddingBottom: "5px"}}
                     src={weatherIcon} className="ml-3" alt=""/>{tempOutside-40}<sup>o</sup>C
              </Form.Label>
              <Form.Control value={tempOutside} onChange={(e)=> {setTempOutside(e.target.value)}} type="range" custom />
            </Form.Group>
          </Form.Row>
        </Form>
      </div>
    </div>
  );
}

export default App;
